@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap);
.constructor__params {
  display: grid;
  grid-template-columns: 700px auto;
  margin-top: 25px;
  padding: 20px 25px;
  min-height: 60px;
  background: #FFFFFF;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.15); }
  .constructor__params .about {
    width: 100%; }
    .constructor__params .about .title input[type=text] {
      border: none;
      font-weight: 500;
      font-size: 20px;
      line-height: 23px;
      letter-spacing: 0.02em;
      width: 100%;
      outline: none;
      text-transform: uppercase; }
    .constructor__params .about .description {
      margin-top: 9px; }
      .constructor__params .about .description input[type=text] {
        border: none;
        font-size: 16px;
        line-height: 21px;
        letter-spacing: 0.02em;
        color: #606060;
        width: 100%;
        outline: none; }
  .constructor__params .color {
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: flex-end; }
    .constructor__params .color button {
      cursor: pointer;
      width: 26px;
      height: 26px;
      border-radius: 50%;
      border: none;
      margin-right: 10px;
      outline: none; }

.constructor__questions .filters {
  display: flex; }
  .constructor__questions .filters > div {
    margin-right: 20px; }

.constructor__questions .table {
  margin-top: 25px; }

.constructor__questions {
  padding: 20px 25px;
  margin-top: 20px;
  margin-bottom: 80px;
  background: #FFFFFF;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.15); }

.constructor__submit {
  position: fixed;
  width: 100%;
  left: 0px;
  bottom: 0px;
  -webkit-transition: .4s;
  transition: .4s; }
  .constructor__submit .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
    width: 910px;
    min-height: 40px;
    padding: 20px 25px;
    background: #FFFFFF;
    box-shadow: 0px -1px 8px rgba(0, 0, 0, 0.15); }
    .constructor__submit .container .title {
      font-weight: 500;
      font-size: 16px;
      line-height: 23px;
      letter-spacing: 0.02em; }

.constructor {
  color: #2c2c2c;
  -webkit-transition: .4s;
  transition: .4s; }
  .constructor main {
    max-width: 960px;
    margin-left: auto;
    margin-right: auto; }

.trainer__nav {
  display: flex;
  justify-content: space-between;
  margin-top: 25px;
  padding: 20px 25px;
  min-height: 40px;
  background: #FFFFFF;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.15); }

.trainer__main {
  margin-top: 25px;
  padding: 20px 25px;
  min-height: 40px;
  background: #FFFFFF;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.15); }
  .trainer__main .info {
    display: flex;
    justify-content: space-between; }
    .trainer__main .info .question .title {
      font-weight: 500;
      font-size: 16px;
      line-height: 23px;
      letter-spacing: 0.02em; }
    .trainer__main .info .question .text {
      margin-top: 10px; }
  .trainer__main .decision {
    margin-top: 30px; }
    .trainer__main .decision .actions {
      display: flex;
      justify-content: flex-end;
      margin-top: 15px; }
  .trainer__main .table {
    margin-top: 20px; }

.trainer {
  color: #2c2c2c; }
  .trainer main {
    max-width: 960px;
    margin-left: auto;
    margin-right: auto; }
  .trainer .loading {
    position: absolute;
    top: 50%;
    left: 50%; }

body {
  margin: 0; }
  body * {
    font-family: 'Roboto', sans-serif; }

